/* stylelint-disable rule-empty-line-before */
.table {
  width: 542px;
  height: 542px;
  margin: 30px auto 20px;
  border: 1px #a5a5a5 solid;
  border-radius: 4px;
}
.row {
  height: 60px;
}
.row.line {
  border-top: #000 2px solid;
}
.cell.column {
  border-left: #000 2px solid;
}
.cell {
  display: inline-block;
  border: 1px #a5a5a5 solid;
  border-radius: 4px;
}
.content {
  display: block;
  width: 58px;
  height: 58px;
  line-height: 58px;
  font-size: 26px;
  float: left;
  text-align: center;
}
.nullClass {
  background: #ddd;
}
.wrongNumber {
  background: lightcoral !important;
}
.sameNumber {
  background: #8effa0;
}
.cellClick {
  background: #61fc7b;
}
.chooseNumber {
  width: 45px;
  height: 45px;
  background: lightgray;
  border-radius: 3px;
  font-size: 22px;
}
.clickButton {
  position: absolute;
  right: 120px;
}
.chooseLevel {
  position: absolute;
  top: 200px;
  right: 120px;
  width: 100px;
}
.reset {
  top: 100px;
}
.clear {
  top: 50px;
}
.timeUsed {
  top: 150px;
}
.numbers {
  width: 100%;
  display: flex;
  justify-content: center;
}
body[arco-theme='dark'] .row.line {
  border-top: #fff 2px solid;
}
body[arco-theme='dark'] .cell.column {
  border-left: #fff 2px solid;
}
